import React, {useState} from "react"
import {Link, graphql} from "gatsby"
import Navigation from "../components/layout/Navigation";
import Img from "gatsby-image";
import {useTranslation} from "react-i18next";
import '../i18n/i18n';
import FadedText from "../components/elements/FadedText";
import ButtonBack from "../components/elements/BackButton";
import {SEO} from "../components/seo";

const BlogIndex = props => {
    const {data} = props
    const allPosts = data.allWpPost.nodes
    const emptyQuery = ""
    const [state, setState] = useState({
        filteredData: [],
        query: emptyQuery,
    })
    const {t} = useTranslation();
    const [filterCategory, setFilterCategory] = useState('');
    const categoriesArray = [
        {category: 'technologies', name: 'Technologies <br> Softwares <br> Solutions', image: '', title: 'Technologies Softwares Solutions', subtitle: ''},
        {category: 'explains', name: '<img width="70%" src="/images/brand/logo_text_White-full.svg"> explains', image: '', title: 'KALMARS Explains', subtitle: 'Introduce yourself to various concepts and terminology used in development world'},
        {category: 'news', name: 'News and <br> updates!', image: '', title: 'News and updates!', subtitle: ''},
    ]

    function handleInputChange(val) {
        const query = document.getElementById("js--search-bar").value;
        const {data} = props
        const posts = data.allWpPost.nodes || []
        const filteredData = posts.filter(post => {
            const title = post?.title
            const tags = post?.tags.nodes
            const excerpt = post?.excerpt
            return (
                excerpt.toLowerCase().includes(query.toLowerCase()) ||
                title.toLowerCase().includes(query.toLowerCase()) ||
                (tags &&
                    tags
                        .join("")
                        .toLowerCase()
                        .includes(query.toLowerCase()))
            )
        })
        setState({
            query,
            filteredData,
        })
    }

    function clearFilter() {
        let input_el = document.getElementById("js--search-bar");
        setFilterCategory('');
        input_el.value = '';
        handleInputChange();
    }

    const {filteredData, query} = state
    const hasSearchResults = filteredData && query !== emptyQuery
    const posts = hasSearchResults ? filteredData : allPosts

    return (
        <>
            <Navigation pageTitle={'Blogs'} pageNr={'MX6B1'}/>
            <FadedText text={'EXPAND'} direction="bottom"/>
            <div className="relative py-14 min-h-screen" id="blogs">
                <div className="md:container mx-auto mt-20 md:mt-18">
                    <h1 className="text-4xl md:text-5xl glitch text-center no-padding" data-glitch={filterCategory ? categoriesArray.filter((item) => item.category === filterCategory)[0].title : t('blogs')}>
                        {filterCategory ? categoriesArray.filter((item) => item.category === filterCategory)[0].title : t('blogs')}
                    </h1>
                    <h2 className="text-2xl pb-8 text-white md:text-3xl text-center">
                        {filterCategory ? categoriesArray.filter((item) => item.category === filterCategory)[0].subtitle : t('blogs_subtitle')}
                    </h2>
                    <div className="text-end">

                    </div>
                    <div className="flex justify-end">
                        <div>
                            <input
                                className="css--search-input w-full"
                                id="js--search-bar"
                                type="text"
                                aria-label="Search"
                                placeholder="Search..."
                                onChange={() => handleInputChange()}
                            />
                        </div>
                    </div>
                    {(filterCategory || query) &&
                    <span onClick={() => clearFilter()} className="flex">
                        <ButtonBack  text={t('return')}/>
                    </span>
                    }
                    <ul className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-2 mt-12 px-4 md:px-0" id="news_block">
                        {!filterCategory && !query &&
                        categoriesArray.map(category => (
                            <li key={category.category} className="aspect-square">
                                <div onClick={() => setFilterCategory(category.category)} className="col-span-1 flex flex-col h-full absolute w-full leading-8 css--blog-bg">
                                    <div className="relative h-full">
                                        <div className="h-full flex justify-center items-center absolute top-0 w-full p-7">
                                            <div className="col-span-2 w-100">
                                                <h2 className="text-2xl xl:text-4xl flex items-center flex-col text-center text-white font-bold hover:text-cyan-500" dangerouslySetInnerHTML={{__html: category.name}}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </li>
                        ))
                        }
                        {posts.map(node => (
                            ((node.tags.nodes.filter((item) => item.name === filterCategory).length > 0 && filterCategory) || (!filterCategory && query)) &&
                            <li key={node.slug} className="aspect-square">
                                <Link to={node.slug + "/"}>
                                    <div className="col-span-1 flex flex-col h-full absolute w-full leading-8">
                                        <div className="relative h-full">
                                            <Img className="object-cover h-full w-full" fluid={node.featuredImage.node.localFile.childImageSharp.fluid} alt="Blog image"/>
                                            <div className="h-full absolute top-0 w-full p-7">
                                                <div className="col-span-2">
                                                    <h2 className="text-2xl xl:text-4xl text-white font-bold hover:text-cyan-500">{node.title}</h2>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </Link>
                            </li>
                        ))}
                    </ul>
                </div>
            </div>
        </>
    )
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    allWpPost(
    sort: { order: DESC, fields: [date] }
    filter: {categories: {nodes: {elemMatch: {name: {eq: "blogs"}}}}}
    ) {
       nodes {
            id
            title
            slug
            excerpt
            date(formatString: "YYYY/MM/DD")
            tags {
              nodes {
                name
              }
            }
            language {
              slug
            }
            featuredImage {
              node {
                sourceUrl
                 localFile {
                   publicURL
                   childImageSharp {
                     gatsbyImageData(
                        placeholder: BLURRED
                     )
                     fluid(maxWidth: 2000, quality: 100) {
                         ...GatsbyImageSharpFluid
                     }
                   }
                 }
              }
            }
          }
    }
  }
`
export const Head = () => <SEO title={"Blogs"} description={'Welcome to the KALMARS IT development agency blog, which discusses a range of subjects relating to the digital world, website development, and the IT industry.'} keywords={'web developer site, web development blog, software development'}/>

